body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  height: 100vh;
}

div.enemyGroupImage {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

div.enemyGroupImage, img.enemyImage {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  object-fit: scale-down;
}

img.enemyImage {
  width: 16%;
}

